<template>
    <div class="list" ref="list">
        <div>
            <div class="area">
                <div class="title border-topbottom">当前城市</div>
                <div class="button-list">
                    <div class="button-box">
                        <div class="button">{{this.city}}</div>
                    </div>
                </div>
            </div>
            <div class="area">
                <div class="title border-topbottom">热门城市</div>
                <div class="button-list">
                    <div class="button-box" v-for="item in hot" :key="item.id">
                        <div class="button" v-on:click="handleCity(item.name)">{{item.name}}</div>
                    </div>
                </div>
            </div>
            <div class="area" v-for="(item,key) in cities" :key="key" :ref="key">
                <div class="title border-topbottom">{{key}}</div>
                <div class="item-list">
                    <div class="item border-bottom" v-for="innerItem in item" :key="innerItem.id"  v-on:click="handleCity(innerItem.name)">{{innerItem.name}}</div>
                </div>
            </div>
        </div>

    </div>
</template>

<script type="text/ecmascript-6">
    import BScroll from 'better-scroll'
    import { mapMutations,mapState } from 'vuex'
    export default {
        name: 'CityList',
        data() {
            return {}
        },
        props: {
            hot: Array,
            cities: Object,
            letter: String
        },
        watch: {
            letter(newValue,oldValue){
//                this.foods_scroll.scrollToElement(this.$refs[newValue][0],300)
//                console.log( this.letter == newValue ); // true
                if(this.letter){
                    this.foods_scroll.scrollToElement(this.$refs[this.letter][0],300)
                }
            }
        },
        mounted(){
            this.foods_scroll = new BScroll(this.$refs.list,{click: true})
        },
        methods: {
            handleCity(city){

                this.increment(city)
                this.$router.push('/')
            },
            ...mapMutations(['increment'])
        },
        computed: {
            ...mapState(['city']),
        }
    }
</script>

<style scoped>
    .list{
        position: absolute;
        top: 1.58rem;
        left: 0;
        right: 0;
        bottom: 0;
        overflow: hidden;
    }
    .area{}
    .area .title{
        line-height: .54rem;
        padding-left: .2rem;
        background: #eeeeee;
        color: #666;
        /*margin: .1rem 0;*/
        font-size: .26rem;
    }
    .area .border-topbottom:after, .area .border-topbottom:before{
        border-color: #cccccc;
    }
    .area .button-list{
        padding:  .1rem .4rem .1rem .1rem;
        overflow: hidden;
    }
    .area .button-list .button-box{
        float: left;
        width: 33.33%;
    }
    .area .button-list .button-box .button{
        margin: .1rem;
        padding: .1rem 0;
        text-align: center;
        border: .02rem solid #cccccc;
        border-radius: .1rem;
    }
    .area .item-list{

    }
    .area .item-list .item{
        padding-left: .2rem;
        line-height: .76rem;
    }
    .area .item-list .border-bottom:before{
        border-color: #ccc
    }
</style>
