<template>
    <div>
        <city-header></city-header>
        <city-search :cities="cities"></city-search>
        <city-list :hot="hotCities" :cities="cities" :letter="letter"></city-list>
        <city-alphabet v-on:change="getLetter" :cities="cities"></city-alphabet>
        <div v-if="!this.cities" class="iconfont xz">&#xe657;</div>
    </div>
</template>

<script type="text/ecmascript-6">
    import CityHeader from './components/Header.vue'
    import CitySearch from './components/Search.vue'
    import CityList from './components/List.vue'
    import CityAlphabet from './components/Alphabet.vue'
    export default {
        name: 'City',
        data() {
            return {
                hotCities: null,
                cities: null,
                letter: ''
            }
        },
        components: {
            CityHeader,
            CityList,
            CitySearch,
            CityAlphabet
        },
        mounted(){
            this.getAjax()
        },
        activated(){

//            this.getAjax()
        },
        methods: {
            getAjax(){
                this.$http.get('/data/city.json')
                    .then((res) => {
                        res = res.data
                        if(res.ret && res.data){
                            var data = res.data
                            this.cities = data.cities
                            this.hotCities = data.hotCities
                        }
                    }, (err) => {
                        console.log(err);
                    })
            },
            getLetter(letter){
                this.letter = letter
            }
        }
    }
</script>

<style scoped>
    @keyframes move {
        from{
            transform:rotate(0deg);
        }
        to{
            transform:rotate(360deg);
        }
    }
    .xz{
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%);
        font-size: .8rem;
        animation: move 1.8s linear infinite;
    }
</style>
