<template>
    <div class="box">
        <div class="search">
            <input type="text" placeholder="请输入城市名称" v-model="inputData">
        </div>
        <div class="search-content" ref="list" v-show="inputData">
            <ul>
                <li class="item borderbottom" v-for="item in list" :key="item.id" v-on:click="handleSearchCity(item.name)">{{item.name}}</li>
                <li class="item borderbottom" v-show="isShowLi">没有找到匹配的内容</li>
            </ul>
        </div>
    </div>

</template>

<script type="text/ecmascript-6">
    import BScroll from 'better-scroll'
    export default {
        name: 'CitySearch',
        data() {
            return {
                inputData: '',
                list: [],
                isShowLi: false,
                timer: null
            }
        },
        props: {
            cities: Object
        },
        watch: {
            inputData(){
//                if(this.timer){
//                    clearTimeout(this.timer)
//                }

                if(!this.inputData){
                    this.list = []
                    return
                }
                this.isShowLi = false

//                this.timer = setTimeout(()=>{
                    var arr = []
                    for(var key in this.cities){
                        this.cities[key].forEach((ele,index)=>{
                            if( ele.name.indexOf(this.inputData) > -1 || ele.spell.indexOf(this.inputData) > -1 ){
                                arr.push(ele)
                            }
                        })
                    }
                    this.list = arr
                    this.isShowLi = arr.length == 0 ? true : false
//                },100)
            }
        },
        mounted(){
            this.foods_scroll = new BScroll(this.$refs.list,{click: true})
        },
        methods: {
            handleSearchCity(city){
                this.$store.commit('increment',city)
                this.$router.push('/')
            }
        },
        activated(){
            this.inputData = ''

        },
        computed: {
            isShow(){
                return this.list.length
            }
        }
    }
</script>

<style scoped>
    .box{
        /*position: relative;*/
        z-index: 2;
    }
    .search{
        height: .72rem;
        padding: 0 .1rem;
        background: #00bcd4;
        position: relative;
        z-index: 2;
    }
    .search input{
        box-sizing: border-box;
        width: 100%;
        height: .62rem;
        line-height: .62rem;
        padding: 0 .1rem;
        text-align: center;
        color: #666;
        border-radius: .1rem;
    }
    .search-content{
        position: absolute;
        top: 1.58rem;
        left: 0;
        right: 0;
        bottom: 0;
        background: #cccccc;
        z-index: 2;
        overflow: hidden;
    }
    .search-content .item{
        padding-left: .2rem;
        line-height: .68rem;
        background: #eeeeee;
    }
</style>
