<template>
    <div class="container">
        <div class="box">
            <swiper :options="swiperOption" v-if="imgs.length>0">
                <swiper-slide v-for="(item,index) in imgs" v-bind:key="index">
                    <img :src="item" alt="">
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
        <div class="clone" v-on:click="cloneGallary">X</div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: 'Gallary',
        data() {
            return {
                swiperOption: {
                    pagination: '.swiper-pagination',
                    paginationType : 'fraction',
                    observer:true,
                    observeParents:true
                }
            }
        },
        props: {
            imgs: {
                type: Array,
                default(){
                    return []
                }
            }
        },
        methods: {
            cloneGallary(){
                this.$emit('close')
            }
        },
        computed: {

        }
    }
</script>

<style scoped>
    .container >>> .swiper-container{
        overflow: visible;
    }
    .container{
        display: flex;
        align-items: center;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 10;
        background: #000;
    }
    .container .box{
        width: 100%;
        height: 0;
        padding-bottom: 66.7%;
        /*overflow: hidden;*/
        /*background: url(../../assets/image/banner1.jpg) no-repeat center / cover;*/
        /*background: #fff;*/
    }
    .container .box img{
        width: 100%;
        /*display: block;*/
    }
    .swiper-pagination{
        color: #fff;
        bottom: -1rem;
    }
    .container .clone{
        position: absolute;
        bottom: .3rem;
        left: 50%;
        transform: translate(-50%);
        width: 1rem;
        height: 1rem;
        line-height: 1rem;
        text-align: center;
        border-radius: 50%;
        color: #fff;
        font-size: .5rem;
