<template>
    <div class="wrapper">
        <swiper :options="swiperOption">
            <swiper-slide v-for="item in slidesImg" v-bind:key="item.id">
                <img class="swiper-img" v-bind:src="item.imgUrl" alt="">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>

</template>

<script type="text/ecmascript-6">
    export default {
        name: 'HomeSwiper',
        data () {
            return {
                
                slidesImg: [
                    {
                        id: '1',
                        imgUrl: require('../../../assets/image/slide1.png')
                    },
                    {
                        id: '2',
                        imgUrl: require('../../../assets/image/slide2.png')
                    },
                    {
                        id: '3',
                        imgUrl: require('../../../assets/image/slide3.png')
                    }
                ],
                swiperOption: {
                    loop: true,
                    pagination: '.swiper-pagination',
                    autoplay: 3000,
                    autoplayDisableOnInteraction: false,

                }
            }
        },
        props: {
            slidesList: {
                type: Array
            }
        }
    }
</script>

<style scoped>
    .wrapper {
        width: 100%;
        height: 0;
        overflow: hidden;
        padding-bottom: 26.6666%;
        background: #eeeeee;
    }

    .wrapper >>> .swiper-pagination-bullet.swiper-pagination-bullet-active {
        background: #ffffff;
        opacity: 1;
    }

    .wrapper >>> .swiper-pagination-bullet{
        background: #ffffff;
        opacity: 0.5;

    }

    .swiper-img {
        width: 100%;
    }
</style>
