import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

var defaultCity = '广州'
try {
    if (localStorage.city) {
        defaultCity = localStorage.city
    }
} catch (e) {

}

export default new Vuex.Store({
    state: {
        city: defaultCity
    },
    mutations: {

        increment (state, city) {
            state.city = city

            try {
                localStorage.city = city
            } catch (e) {

            }
        }
    }
})
