<template>
    <div class="icons">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(items,index) in pages" v-bind:key="index">
                <div class="icon-item" v-for="item in items" :key="item.id">
                    <div class="icon-img-box">
                        <img :src="item.imgUrl" alt="" class="icon-img">
                    </div>
                    <p class="icon-text">{{item.desc}}</p>
                </div>
            </swiper-slide>

            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>


    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name:'HomeIcons',
        data() {
            return {
                iconsImg: [
                    {
                        id: '1',
                        imgUrl: require('../../../assets/image/icons1.png'),
                        desc: '景点门票'
                    },
                    {
                        id: '2',
                        imgUrl: require('../../../assets/image/icons2.png'),
                        desc: '必游榜单'
                    },
                    {
                        id: '3',
                        imgUrl: require('../../../assets/image/icons3.png'),
                        desc: '踏青赏花'
                    },
                    {
                        id: '4',
                        imgUrl: require('../../../assets/image/icons4.png'),
                        desc: '一日游'
                    },
                    {
                        id: '5',
                        imgUrl: require('../../../assets/image/icons5.png'),
                        desc: '动物植园'
                    },
                    {
                        id: '6',
                        imgUrl: require('../../../assets/image/icons6.png'),
                        desc: '故宫'
                    },
                    {
                        id: '7',
                        imgUrl: require('../../../assets/image/icons7.png'),
                        desc: '主题乐园'
                    },
                    {
                        id: '8',
                        imgUrl: require('../../../assets/image/icons8.png'),
                        desc: 'Q+精选'
                    },
                    {
                        id: '9',
                        imgUrl: require('../../../assets/image/icons9.png'),
                        desc: '自然风光'
                    }
                ],
                swiperOption: {
                    pagination: '.swiper-pagination',
                }
            }
        },
        computed: {
            pages(){
                var arr = []

                for(var i = 0; i<this.iconsImg.length; i++){
                    var y = Math.floor(i/8)
                    if(!arr[y]){
                        arr[y] = []
                    }
                    arr[y].push(this.iconsImg[i])
                }

                return arr
            }
        }
    }
</script>

<style scoped>
    .icons >>> .swiper-container{
        height: 0;
        padding-bottom: 55%;
        /*background: #aaa;*/
    }
    .icons{
        height: 0;
        padding-bottom: 55%;
        padding-top: .1rem;
        overflow: hidden;
        /*background: #000;*/
    }
    .icons .icon-item{
        float: left;
        width: 25%;
        height: 0;
        padding-bottom: 24%;
        overflow: hidden;
        position: relative;
        /*background: #ccc;*/
    }
    .icons .icon-item .icon-img-box{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: .5rem;
        overflow: hidden;
        padding: .1rem;
        /*text-align: center;*/
        /*background: #ddd;*/
    }
    .icons .icon-item .icon-img-box .icon-img{
        height: 100%;
        display: block;
        margin: 0 auto;
    }
    .icons .icon-item .icon-text{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        text-align: center;
        height: .5rem;
        line-height: .5rem;
        /*background: #aaa;*/
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
</style>
