<template>
    <div>
        <router-link to="/" tag="div" class="header-abs" v-show="isShow">
            <span class="iconfont header-abs-back">&#xe624;</span>
        </router-link>
        <div class="header-fixed" v-show="!isShow" :style="opacityStyle">
            <router-link to="/">
                <span class="iconfont header-fixed-back">&#xe624;</span>
            </router-link>
            景点详情
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: 'DetailHeader',
        data() {
            return {
                isShow: true,
                opacityStyle: {
                    opacity: 1
                }
            }
        },
        methods: {
            handleScroll(){
                var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0
                console.log(scrollTop)
                if(scrollTop>40){
                    this.isShow = false
                    this.opacityStyle.opacity = (scrollTop - 50)/150 + 0.2
                }else{
                    this.isShow = true
                }
            }
        },
        activated(){
            window.addEventListener('scroll',this.handleScroll)
        },
        deactivated(){
            window.removeEventListener('scroll',this.handleScroll)
        }
    }
</script>

<style scoped>
    .header-abs{
        position: absolute;
        top: .2rem;
        left: .2rem;
        width: .8rem;
        height: .8rem;
        line-height: .8rem;
        border-radius: .4rem;
        text-align: center;
        background: rgba(0,0,0,0.6);
    }
    .header-abs .header-abs-back{
        color: #fff;
    }
    .header-fixed{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 2;
        height: .86rem;
        line-height: .86rem;
        text-align: center;
        background: #00bcd4;
        font-size: .32rem;
        color: #fff;
    }
    .header-fixed .header-fixed-back{
        position: absolute;
        top: 0;
        left: 0;
        width: .64rem;
        color: #fff;
    }
</style>
