<template>
    <div class="home">
        <home-header></home-header>
        <home-swiper :slidesList="slidesList"></home-swiper>
        <home-icons></home-icons>
        <home-recommend></home-recommend>
        <home-weekend></home-weekend>
    </div>
</template>

<script type="text/ecmascript-6">
    import HomeHeader from './components/Header.vue'
    import HomeSwiper from './components/Swiper.vue'
    import HomeIcons from './components/Icons.vue'
    import HomeRecommend from './components/Recommend.vue'
    import HomeWeekend from './components/Weekend.vue'
    import { mapState } from 'vuex'
    export default {
        name: 'Home',
        data() {
            return {
                slidesList: [],
                lastCity: ''
            }
        },
        components: {
            HomeHeader,
            HomeSwiper,
            HomeIcons,
            HomeRecommend,
            HomeWeekend
        },
        computed: {
                ...mapState(['city'])
        },
        mounted () {
//            this.lastCity = this.city
//            this.getAjax()
        },
        methods: {
            getAjax(){
                var that = this
                this.$http.get('/test.php?city='+this.city)
                        .then(function(res){

                            res = res.data

                            if(res.ret && res.data){
                                var data = res.data
                                console.log(data);

                                that.slidesList = data.slidesList
                            }
                        })
            },
            getSuccess(){

            }
        },
        activated(){
            if(this.lastCity !== this.city){
                this.lastCity = this.city
//                this.getAjax()
            }

//            console.log('home-activated')
        }
    }
</script>

<style scoped>
    .home {
        max-width: 750px;
        margin: 0 auto;
    }
</style>
