<template>
    <div ref="list" class="list">
        <div class="item"
             v-for="(item,index) in letters"
             :ref="item"
             v-on:click="getInnerText(item)"
             v-on:touchstart="handleTouchStart()"
             v-on:touchmove.prevent="handleTouchMove($event)"
             v-on:touchend="handleTouchEnd()"
        >
            {{item}}
        </div>
        <div class="contentCenter">{{letter}}</div>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: 'CityAlphabet',
        data() {
            return {
                alphabetArr: ['A','B','C','D','E','F','G','H','J','K','L','M','N','P','Q','R','S','T','W','X','Y','Z'],
                touchStatus: false,
                listTop: 0,
                itemHeight: 0,
                itemTop: 0,
                timer: null,
                letter: ''
            }
        },
        props: {
            cities: Object
        },
        methods: {
            getInnerText(key){
                this.$emit('change',key)

            },
            handleTouchStart(){
                this.touchStatus = true
            },
            handleTouchMove(event){
                if(this.touchStatus){

//                    if(this.timer){
//                        clearTimeout(this.timer)
//                    }

//                    this.timer = setTimeout(()=>{
                        var index = Math.floor((event.touches[0].clientY - this.listTop - this.itemTop)/this.itemHeight)

                        if(index>=0 && index<this.letters.length){
                            this.letter = this.letters[index]
                            this.$emit('change',this.letters[index])
                        }
//                    },16)

                }

            },
            handleTouchEnd(){
                this.letter = ''
                this.touchStatus = false
            }
        },
        mounted(){


        },
        updated(){
            this.listTop = this.$refs.list.offsetTop
            var item = this.$refs.A[0]
            this.itemHeight = item.offsetHeight
            this.itemTop = item.offsetTop
        },
        computed: {
            letters(){
                var arr = []
                for(var key in this.cities){
                    arr.push(key)
                }
                return arr
            }
        }
    }
</script>

<style scoped>
    .list{
        position: absolute;
        top: 1.58rem;
        right: 0;
        bottom: 0;
        /*width: .4rem;*/
        /*height: 100%;*/
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: .3rem 0;
    }
    .list .item{
        flex: 1;
        /*line-height: .45rem;*/
        /*height: .45rem;*/
        padding: 0 .2rem;
        text-align: center;
        color: #00bcd4;
        font-size: .28rem;
    }
    .contentCenter{
        position: fixed;
        top: 30%;
        left: 50%;
        transform: translate(-50%);
        font-size: .64rem;
        color: #00bcd4;
    }
</style>
